import Grid from "@material-ui/core/Grid";
import React from "react";
import AchievementWidget from "../components/AchievementWidget";
import AdminAppBar from "../components/AdminAppBar";
import AdminToolbar from "../components/AdminToolbar";
import FollowersWidget from "../components/FollowersWidget";
import MeetingWidgets from "../components/MeetingWidgets";
import PersonalTargetsWidget from "../components/PersonalTargetsWidget";
import RecentNotifications from "../components/RecentNotifications";
import ViewsWidget from "../components/ViewsWidget";
import WelcomeWidget from "../components/WelcomeWidget";

const Home = () => {
  return (
    <React.Fragment>
      <AdminAppBar>
        <AdminToolbar>
          <RecentNotifications />
        </AdminToolbar>
      </AdminAppBar>
      <Grid container spacing={3}>
        <Grid item xs={12} md={6} lg={4}>
          <WelcomeWidget />
          <AchievementWidget />
        </Grid>
        <Grid item xs={12} md={6} lg={4}>
          <FollowersWidget />
          <ViewsWidget />
        </Grid>
        <Grid item xs={12} md={6} lg={4}>
          <PersonalTargetsWidget />
          <MeetingWidgets />
        </Grid>
      </Grid>
    </React.Fragment>
  );
};

export default Home;
